import ChinaAddressV3Data from '../../../assets/js/address.json'
import Vue from 'vue'
Vue.use(ConfirmPlugin)
export default {
components: {
XSwitch,
XAddress ,
Badge ,
XHeader,
AlertPlugin,
Cell ,
XButton,
},
data() {
return {
value: '',
value2: [],
